/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";
import React, { useState } from "react";

import ModalFairness from "./modals/ModalFairness";


function BetsList(props) {

    const [showModal, setShowModal] = useState(false)
    const openModal = () => setShowModal(true)
    const closeModal = () => setShowModal(false)

    const styles = {
        bets: css({
            padding: "16px 15px 27px 15px",
            backgroundColor: `${props.colors.blocksBg}`,
            position: "relative",
            // maxHeight: "calc(100vh - 30px)", 
            display: "grid",
            gridTemplateRows: "auto 1fr",
        }),
        item: css({
            padding: "3px 3px",
            display: "grid",
            gridTemplateColumns: "30px 60px 1fr 1fr 1fr",
            marginBottom: "9px",
            borderRadius: "8px",
            background: "linear-gradient(90deg, #1E183B 3.37%, #2D255B 98.06%)",
            border: "1px solid linear-gradient(90deg, #1E183B 3.37%, #2D255B 98.06%)",
        }),
        item_active: css({
            padding: "3px 3px",

            borderRadius: "8px",

            display: "grid",
            gridTemplateColumns: "30px 60px 1fr 1fr 1fr",
            marginBottom: "9px",
            background: "#35205b",
            border: "1px solid #753ec2",
        }),
        item_name: css({
            alignSelf: "center",

            marginLeft: "6px",
            fontWeight: 600,
            fontSize: "12px",
            color: "#9287c2",
            textWrap: "nowrap",
        }),
        item_money: css({
            alignSelf: "center",
            textAlign: "center",
            marginLeft: "4px",
            fontWeight: 600,
            fontSize: "11px",
            color: "#fff",
        }),
        item_ratio: css({
            alignSelf: "center",

            fontSize: "12px",
            fontWeight: "600",
            lineHeight: "normal",
            textAlign: "center",
            color: "#fff",

            alignContent: "center",

            justifyContent: "center",
            display: "flex",
        }),
        item_ratio_active: css({
            alignSelf: "center",

            lineHeight: "normal",
            textAlign: "center",

            display: "block",
            width: "fit-content",
            height: "fit-content",

            borderRadius: "9.98px",
            background: "#753EC2",
            padding: "5px 15px",
        }),
        item_earn: css({
            alignSelf: "center",

            textAlign: "center",
            marginLeft: "6px",
            color: "#E38A00",
            fontSize: "13px",
            fontWeight: "600",
        }),
        tops_item: css({
            display: "flex",
            justifyContent: "space-between",
            marginBottom: "10px",
            padding: "7px 12px 7px 12px",
            borderRadius: "18px",
            background:
                "linear-gradient(92deg, #1E183B 18.86%, #2D255B 130.49%)",
        }),
        tops_person: css({
            display: "grid",
            gridTemplateRows: "auto auto",
        }),
        tops_personName: css({
            marginLeft: "10px",
            color: "#9287c2",
            fontWeight: "500",
        }),
        tops_wrapper: css({
            display: "grid",
            gridTemplateColumns: "repeat(2, auto)",
            gridGap: '7px',
            width: "fit-content",
            
        }),
        tops_info: css({
            display: "flex",
            flexDirection: "column",
        }),
        tops_infoItem: css({
            padding: "9px",
            marginBottom: "3px",
            color: "#fff",
            borderRadius: "542px",
            background: "#151027",
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
        }),
        tops_infoName: css({
            color: "#9287C2",
            fontSize: "12px",
            fontWeight: "600",
        }),
        tops_infoValue: css({
            color: "#e38a00",
            fontSize: "11px",
            fontWeight: "600",
        }),
        tops_infoWin: css({
            fontSize: "8px",
            fontWeight: "600",
        }),
        tops_infoCoef: css({
            color: "#E38A00",
            fontSize: "11px",
            fontStyle: "normal",
            fontWeight: "700",
        }),
        tops_options: css({
            // display: "flex",
            // alignItems: "center",

            display: 'grid',
            gridTemplateRows: 'repeat(3, 1fr)',
            gap: '2px',
            width: '27px',
            padding: '2px',
            borderRadius: '4px',
            background: '#151027'
        }),
        tops_optionsBtn: css({
            background: "none",
            border: "none",
            cursor: "pointer",
            // marginLeft: "5px",
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
            background: '#282150',
            borderRadius: '4px',
        }),
        top: css({
            display: "flex",
            justifyContent: "space-between",
            padding: "10px 0",
        }),
        top__total: css({
            // display: "flex",
            // alignItems: "center",
            
        }),
        total_bets: css({
            marginRight: "5px",
            color: "#666B81",
            fontSize: "11px",
            fontWeight: "700",
        }),
        total_bets_num: css({
            color: "#fff",
            fontSize: "15px",
            fontWeight: "800",
        }),
        top_btn: css({
            display: "flex",
            alignItems: "center",
            background: "#231d47",

            gap: '6px',
            color: "#fff",
            border: "none",
            borderRadius: "5px",
            padding: "5px 10px",
        }),
        top_btn_text: css({
            marginLeft: "5px",
        }),
    };

    const arrayAll = [
        { name: "Ankit", money: 104.85, ratio: null, earn: null },
        { name: "Sgg", money: 104.85, ratio: null, earn: null },
        { name: "Bycal", money: 47.13, ratio: 3.21, earn: 151.28 },
        { name: "Alex", money: 94.25, ratio: 4.97, earn: 468.42 },
        { name: "256077", money: 34.16, ratio: null, earn: null },
        { name: "", money: 35.19, ratio: null, earn: null },
        { name: "Rajive", money: 10.25, ratio: null, earn: null },
        { name: "", money: 43.13, ratio: null, earn: null },
        { name: "", money: 64.77, ratio: null, earn: null },
        { name: "247571712", money: 100.01, ratio: null, earn: null },
        { name: "Girl kutur", money: 88.15, ratio: null, earn: null },
        { name: "-", money: 12.2, ratio: null, earn: null },
        { name: "SWAP", money: 23.3, ratio: null, earn: null },
        { name: "@AYD", money: 78.91, ratio: null, earn: null },
        { name: "Anni", money: 66.15, ratio: null, earn: null },
        { name: "84892", money: 17.14, ratio: null, earn: null },
        { name: "4441", money: 49.1, ratio: null, earn: null },
    ];

    const createBetsListItems = arrayAll.map((item, index) => {
        const { name, money, ratio, earn } = item;
        const imgText = name.slice(0, 2).toUpperCase();

        const outputRatio = () => (ratio === null ? "-" : `${ratio}x`);
        const outputEarn = () => (earn === null ? "-" : `${earn} $`);

        return (
            <div
                css={earn === null ? styles.item : styles.item_active}
                key={`${index}-${name}`}
            >
                <div
                    css={css({
                        background: `#${
                            props.styling.bgColorsArray[
                                props.styling.randomizeBgColor()
                            ]
                        }`,
                        alignSelf: "center",
                        width: "24px",
                        height: "24px",
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        fontSize: "13px",
                        borderRadius: "7px",
                        marginLeft: "3px",
                    })}
                >
                    {imgText}
                </div>
                <p css={styles.item_name}>{name}</p>
                <p css={styles.item_money}>{`${money} $`}</p>
                <p css={styles.item_ratio}>
                    <span
                        css={
                            earn === null
                                ? styles.item_ratio
                                : styles.item_ratio_active
                        }
                    >
                        {outputRatio()}
                    </span>
                </p>
                <p css={styles.item_earn}>
                    <span
                        style={{
                            color: outputEarn() !== "-" ? "#e38a00" : "#ffffff",
                        }}
                    >
                        {outputEarn()}
                    </span>
                </p>
            </div>
        );
    });

    let arrayTop = [
        {
            name: "Marco dinhold",
            bet: 116.65,
            winning: 120_488.94,
            coef: 1032.91,
        },
        {
            name: "RICH KID",
            bet: 94.58,
            winning: 112214.26,
            coef: 1186.5,
        },
        {
            name: "PVBLO",
            bet: 141.4,
            winning: 64126.05,
            coef: 453.51,
        },
        {
            name: "RICH KID",
            bet: 104.23,
            winning: 46458.76,
            coef: 445.72,
        },
        {
            name: "Agilli olungone",
            bet: 118.7,
            winning: 36135.53,
            coef: 304.44,
        },
        {
            name: "-",
            bet: 93.99,
            winning: 38751.38,
            coef: 412.29,
        },
        {
            name: "Gory mahamed",
            bet: 109.91,
            winning: 22461.75,
            coef: 204.36,
        },
        {
            name: "DJBATTINMAMIN",
            bet: 21.29,
            winning: 22375.44,
            coef: 1050.97,
        },
    ];
    let createTopList_items = arrayTop.map((item, id) => {
        let name = item.name;
        let imgText = name.slice(0, 2).toUpperCase();
        let bet = item.bet;
        let winning = item.winning;
        let coef = item.coef;

        let winningOut = () => {
            let splited = winning.toString().split(".");
            let afterDot = splited[1];

            let reversed = splited[0].toString().split("").reverse();

            let reversedAfter = [];
            for (let index = 0; index < reversed.length; index++) {
                reversedAfter.push();
            }

            reversed.forEach((item, id) => {
                if ((id + 1) % 3 === 0) {
                    reversedAfter.push(item);
                    reversedAfter.push(" ");
                } else {
                    reversedAfter.push(item);
                }
            });

            let reversedOut = reversedAfter.reverse().join("");

            let returner = `${reversedOut}.${afterDot}`;

            return returner;
        };

        let styleImg = css({
            background: `#${
                props.styling.bgColorsArray[props.styling.randomizeBgColor()]
            }`,
            width: "30px",
            height: "30px",
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
            fontWeight: "600",
            fontSize: "14px",
            borderRadius: "50%",
        });

        return (
            <div
                className="BetsList__tops-item"
                css={styles.tops_item}
                key={`${id}-${name}`}
            >
                <div className="BetsList__tops-person" css={styles.tops_person}>
                    <div css={styleImg}>{imgText}</div>
                    <p css={styles.tops_personName}>{name}</p>
                </div>

                <div
                    className="BetsList__tops-wrapper"
                    css={styles.tops_wrapper}
                >
                    <div className="BetsList__tops-info" css={styles.tops_info}>
                        <div
                            className="BetsList__info-item"
                            css={styles.tops_infoItem}
                        >
                            <span
                                className="BetsList__info-name"
                                css={styles.tops_infoName}
                            >
                                Bet:
                            </span>
                            <span className="BetsList__info-value">{`${bet} $`}</span>
                        </div>
                        <div
                            className="BetsList__info-item"
                            css={styles.tops_infoItem}
                        >
                            <span
                                className="BetsList__info-name"
                                css={styles.tops_infoName}
                            >
                                Winning:
                            </span>
                            <span
                                className="BetsList__info-value"
                                css={styles.tops_infoWin}
                            >{`${winningOut()} $`}</span>
                        </div>
                        <div
                            className="BetsList__tops-options"
                            css={styles.tops_infoItem}
                        >
                            <span
                                className="BetsList__info-name"
                                css={styles.tops_infoName}
                            >
                                Coef:
                            </span>
                            <span
                                className="BetsList__info-value"
                                css={styles.tops_infoCoef}
                            >{`x${coef}`}</span>
                        </div>
                    </div>

                    <div
                        className="BetsList__tops-options"
                        css={styles.tops_options}
                    >
                        <button
                            className="BetsList__options-btn"
                            css={styles.tops_optionsBtn}
                        >
                            <svg
                                width="13"
                                height="13"
                                viewBox="0 0 13 13"
                                fill="none"
                                xmlns="http://www.w3.org/2000/svg"
                            >
                                <circle
                                    cx="6.5"
                                    cy="6.5"
                                    r="6.5"
                                    fill="#9287C2"
                                />
                                <path
                                    d="M6.12928 11.4243H7.57072L7.4673 5.18667H6.2327L6.12928 11.4243ZM6.84677 1.99998C6.3749 1.99998 6 2.36196 6 2.83382C6 3.30569 6.3749 3.66766 6.84677 3.66766C7.33156 3.66766 7.7 3.30569 7.7 2.83382C7.7 2.36196 7.33156 1.99998 6.84677 1.99998Z"
                                    fill="#241E48"
                                />
                            </svg>
                        </button>
                        <button
                            className="BetsList__options-btn"
                            css={styles.tops_optionsBtn}
                            onClick={openModal}
                        >
                            <svg
                                width="14"
                                height="14"
                                viewBox="0 0 14 14"
                                fill="none"
                                xmlns="http://www.w3.org/2000/svg"
                            >
                                <path
                                    d="M9.49305 14H4.49084C-0.0444922 10.2991 -0.289045 4.96795 0.155596 2.76501L7 0L13.4948 2.76501C15.3623 6.99466 11.6051 12.0174 9.49305 14Z"
                                    fill="#9287C2"
                                />
                                <path
                                    fillRule="evenodd"
                                    clipRule="evenodd"
                                    d="M11.3601 5.60673L10.3634 4.65259L6.43496 8.47068L3.57802 6.00253L2.64047 7.01227L6.49112 10.3389L11.3601 5.60673Z"
                                    fill="#241E48"
                                />
                            </svg>
                        </button>
                        <button
                            className="BetsList__options-btn"
                            css={styles.tops_optionsBtn}
                        >
                            <svg
                                width="12"
                                height="10"
                                viewBox="0 0 12 10"
                                fill="none"
                                xmlns="http://www.w3.org/2000/svg"
                            >
                                <path
                                    fillRule="evenodd"
                                    clipRule="evenodd"
                                    d="M1.52771 0.199808C1.53388 0.202321 1.54005 0.204835 1.54623 0.20735L11.1503 4.11715C11.2814 4.1705 11.4119 4.22357 11.5135 4.27532C11.6036 4.3212 11.7929 4.42247 11.9035 4.62479C12.0322 4.86024 12.0322 5.14062 11.9035 5.37607C11.7929 5.57839 11.6036 5.67965 11.5135 5.72553C11.4119 5.77729 11.2814 5.83035 11.1503 5.88371L1.53106 9.7997C1.39618 9.85463 1.26337 9.90873 1.15179 9.9437C1.04835 9.97612 0.8503 10.0318 0.632018 9.97669C0.384328 9.91416 0.164941 9.74473 0.0586359 9.49822C-0.0377158 9.27479 0.0125889 9.06079 0.0384413 8.96254C0.066907 8.85437 0.114489 8.72587 0.160769 8.60089C0.16317 8.59441 0.165567 8.58793 0.167959 8.58147L1.486 5.01966L0.16372 1.41561C0.161351 1.40915 0.158975 1.40268 0.156595 1.3962C0.110822 1.27152 0.063713 1.14321 0.0356134 1.03517C0.010039 0.936832 -0.0394411 0.723217 0.0571564 0.500402C0.163803 0.254407 0.383102 0.085471 0.630544 0.0231845C0.848464 -0.0316705 1.04607 0.0238159 1.14946 0.0562168C1.2608 0.0911074 1.39328 0.145061 1.52771 0.199808ZM1.34585 1.33039L2.48851 4.44486H5.2466C5.56219 4.44486 5.81803 4.69359 5.81803 5.00043C5.81803 5.30726 5.56219 5.556 5.2466 5.556H2.50211L1.35026 8.66869L10.361 5.00043L1.34585 1.33039Z"
                                    fill="#9287C2"
                                />
                            </svg>
                        </button>
                    </div>
                </div>
            </div>
        );
    });

    const [activeTab, setActiveTab] = useState(0);

    const handleTabClick = (index) => {
        setActiveTab(index);
    };

    return (
        <div className="BetsList" css={styles.bets}>
            <div className="BetsList__changer">
                <button
                    className={`BetsList__changer-btn ${
                        activeTab === 0 ? "active" : ""
                    }`}
                    onClick={() => handleTabClick(0)}
                >
                    All
                </button>
                <button
                    className={`BetsList__changer-btn ${
                        activeTab === 1 ? "active" : ""
                    }`}
                    onClick={() => handleTabClick(1)}
                >
                    My
                </button>
                <button
                    className={`BetsList__changer-btn ${
                        activeTab === 2 ? "active" : ""
                    }`}
                    onClick={() => handleTabClick(2)}
                >
                    Top
                </button>
            </div>

            <div className="BetsList__block">
                <div className="BetsList__list">
                    {/* {activeTab === 0 && createBetsListItems} */}

                    {activeTab === 0 && (
                        <div>
                            <div className="BetsList__top" css={styles.top}>
                                <div
                                    className="BetsList__total"
                                    css={styles.top__total}
                                >
                                    <p css={styles.total_bets}>Total bets:</p>
                                    <p css={styles.total_bets_num}>1212</p>
                                </div>

                                <button
                                    className="BetsList__top-btn btn btn_single"
                                    css={styles.top_btn}
                                >
                                    <svg
                                        width="14"
                                        height="14"
                                        viewBox="0 0 14 14"
                                        fill="none"
                                        xmlns="http://www.w3.org/2000/svg"
                                    >
                                        <path
                                            fillRule="evenodd"
                                            clipRule="evenodd"
                                            d="M7 1.4C3.90721 1.4 1.4 3.90721 1.4 7C1.4 10.0928 3.90721 12.6 7 12.6C10.0928 12.6 12.6 10.0928 12.6 7C12.6 3.90721 10.0928 1.4 7 1.4ZM0 7C0 3.13401 3.13401 0 7 0C10.866 0 14 3.13401 14 7C14 10.866 10.866 14 7 14C3.13401 14 0 10.866 0 7ZM7 2.52C7.3866 2.52 7.7 2.8334 7.7 3.22V6.56738L9.83305 7.6339C10.1788 7.80679 10.319 8.22726 10.1461 8.57305C9.97321 8.91883 9.55274 9.05899 9.20695 8.8861L6.68695 7.6261C6.4498 7.50752 6.3 7.26514 6.3 7V3.22C6.3 2.8334 6.6134 2.52 7 2.52Z"
                                            fill="#9287C2"
                                        />
                                    </svg>

                                    <span css={styles.top_btn_text}>
                                        Pre-round
                                    </span>
                                </button>
                            </div>
                            {createBetsListItems}
                        </div>
                    )}
                    {activeTab === 2 && createTopList_items}
                </div>
            </div>

            <ModalFairness isVisible={showModal} closeModal={closeModal}></ModalFairness>
        </div>
    );
}

export default BetsList;
