/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

import Modal from "../Modal";
import limitsLogo from "../../assets/images/menuLimits.svg";

function ModalLimits(props) {

    const styles = {
        modal: css({
            position: 'fixed',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            width: '100%',
            overflowY: 'scroll',
            maxHeight: 'calc(100vh - 40px)',
            // paddingTop: '50px',
            '::-webkit-scrollbar': {
                display: 'none',
            },
        }),
        main: css({
            padding: '16px 9px',
        }),


        block: css({
            borderRadius: '12px',
            background: '#181531',
            padding: '16px 20px 11px',
            color: '#8E84BD',
            fontSize: '13px',
            fontWeight: '700',
            textAlign: 'center',
        }),


    };

    return (

        <Modal icon={limitsLogo} title="GAME LIMITS" stylesCustom={styles.modal} headerCustom={styles.headerCustom} isVisible={props.isVisible} closeModal={props.closeModal}>

            <div className="modalMain" css={styles.main}>
                <div css={styles.block}>
                    Make your first bet
                </div>
            </div>

        </Modal>

    );
}

export default ModalLimits;
