/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

import Modal from "../Modal";
import fairnessLogo from "../../assets/images/fairness.svg";
import copyIcon from "../../assets/images/copyIcon.svg";

function ModalRules(props) {




    const styles = {
        modal: css({
            position: 'fixed',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            maxWidth: '700px',
            width: '100%',
            overflowY: 'scroll',
            maxHeight: 'calc(100vh - 40px)',
            // paddingTop: '50px',
            '::-webkit-scrollbar': {
                display: 'none',
            },
        }),
        headerCustom: css({
            position: 'sticky',
            top: '-0.5px',
            left: 0,
            width: '100%',
        }),
        main: css({
            padding: '14px 14px 14px 17px'
        }),



        title: css({
            marginBottom: '14px',
            fontSize: '15px',
            fontWeight: '700',
        }),
        list: css({
            listStyleType: 'disc',
            paddingLeft: '16px'
        }),
        listItem: css({
            // color: 'rgba(255, 255, 255, 0.56)',
            fontSize: '11px',
            fontWeight: '700',
            listStyleType: 'disc',
            marginBottom: '14px'
        }),
        modalBlock: css({
            borderRadius: '13px',
            background: '#2C2551',
            padding: '16px 10px 10px 10px'
        }),
        form: css({
            marginTop: '14px',
            display: 'grid',
            gridTemplateColumns: '1fr 200px',
            gridGap: '17px'

        }),
        formInput: css({
            padding: '11px 10px 12px 10px',
            borderRadius: '8px',
            background: '#1B1536',
            border: 'none',
            color: '#fff',
            outline: 'none',
            fontSize: '12px',
            fontWeight: '500',
            fontFamily: "SF Pro Display"
        }),
        formButton: css({
            padding: '10px 20px',
            fontSize: '16px',
            fontWeight: '700',
            color: '#fff',
            textTransform: 'uppercase',
            borderRadius: '8px',
            background: 'linear-gradient(273deg, #7941CE 14.03%, #5426D5 85.97%)',
            border: 'none',
        }),
        checkSalt: css({
            marginTop: '6px',
            maxWidth: '310px',
            color: '#FFF',
            fontSize: '12px',
            fontWeight: '400',
        }),
        info: css({
            marginTop: '10px',
            padding: '9px 10px 8px 16px',
            display: 'grid',
            gridTemplateColumns: 'auto 1fr auto',
            fontSize: '12px',
            fontWeight: '700',
            gridGap: '8px',
            alignItems: 'center',
            background: '#1B1536',
            borderRadius: '8px',
        }),
        infoValue: css({
            display: 'block',
            color: '#58507E',
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
            overflow: 'hidden'
        }),
        infoBtn: css({
            marginLeft: '10px',
            background: 'none',
            border: 'none',
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
        }),

        stats: css({
            display: 'flex',
            flexDirection: 'row',
            marginBottom: '20px',
            marginTop: '16px'
        }),
        statsItem: css({
            marginRight: '10px',
            padding: '9px 11px 8px', 
            borderRadius: '8px',
            background: '#413A68',
            fontSize: '12px',
            fontWeight: '700',
            display: 'flex',
            width: 'fit-content'
        }),
        statsSaltValue: css({
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
            overflow: 'hidden',
            maxWidth: '50px'
        })
    };


    const saltObj = {
        name: 'Salt',
        value: '03310505-0bdd-46e5-b809-5faa1ad19e6b+[209.74]+961c327b4d8b22e2f90e6642e8454556'
    }
    const hashObj = {
        name: 'Hash',
        value: '0dd7437102b111062a54902d7fc3286291f7a9b0ffc14dc9253a97f7725a3c0a4740e768f6703c0a4740e768f670'
    }

    const createInfo = (obj) => {
        return (
            <div className="info" css={styles.info}>
                <p>{obj.name}:</p>
                <p css={styles.infoValue}>{obj.value}</p>
                <button css={styles.infoBtn} onClick={() => navigator.clipboard.writeText(obj.value)}><img src={copyIcon} alt="copy value" /></button>
            </div>
        )
    }

    return (

        <Modal icon={fairnessLogo} title="RULES OF THE GAME" stylesCustom={styles.modal} headerCustom={styles.headerCustom} isVisible={props.isVisible} closeModal={props.closeModal}>
            <div className="modalMain" css={styles.main}>
                <div className="modalBlock" css={[
                    styles.modalBlock,
                    css({marginBottom: '16px'}),
                ]}>
                    <p css={styles.title}>Rounds information</p>

                    <div className="stats" css={styles.stats}>
                        <div className="stats__item" css={styles.statsItem}>
                            <p>Round ID</p>
                            <p css={styles.statsSaltValue}>#{saltObj.value}</p>
                        </div>
                        <div className="stats__item" css={styles.statsItem}>
                            <p>Coeff: </p>
                            <p>209.74x</p>
                        </div>
                    </div>

                    {createInfo(saltObj)}
                    {createInfo(hashObj)}

                    <p css={styles.checkSalt}>
                        By copying <b>Salt</b> you can check the operation of the Provably Fairness technology and use the service to compare the results below
                    </p>
                </div>
                <div className="modalBlock" css={styles.modalBlock}>
                    <p css={styles.title}>Verification service</p>

                    <from css={styles.form}>
                        <input css={styles.formInput} type="text" placeholder="Enter the Salt" />
                        <button css={styles.formButton}>Apply</button>
                    </from>
                </div>

                <div css={css({marginTop: '20px', padding: '4px'})}>
                    <p css={styles.title}>We're playing fair. Do you want to know how it works?</p>
                    <ul css={styles.list}>
                        <li css={styles.listItem}>
                            The round in this game ends as soon as the coefficient increases to a certain value.
                        </li>
                        <li css={styles.listItem}>
                            This value is generated on the game servers before the start of the round and is openly shown to all players in the form of an encrypted HASH.
                        </li>
                        <li css={styles.listItem}>
                            At the end of the round, the same HASH will be saved in history along with SALT.
                        </li>
                        <li css={styles.listItem}>
                            To view the cipher, click on the shield icon in your betting history, leaderboard, or history of previous rounds.
                        </li>
                        <li css={styles.listItem}>
                            You can check the hashing correctness in the window Provably Fair or any online calculator.
                        </li>
                    </ul>
                </div>
            </div>

        </Modal>

    );
}

export default ModalRules;
