/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

import Modal from "../Modal";
import rulesLogo from "../../assets/images/rules.svg";

function ModalRules(props) {




   const styles = {
      modal: css({
         position: 'fixed',
         top: '50%',
         left: '50%',
         transform: 'translate(-50%, -50%)',
         maxWidth: '690px',
         width: '100%',
         overflowY: 'scroll',
         maxHeight: 'calc(100vh - 40px)',
         // paddingTop: '50px',
         '::-webkit-scrollbar': {
            display: 'none',
         },
      }),
      headerCustom: css({
         position: 'sticky',
         top: '-0.5px',
         left: 0,
         width: '100%',
      }),
      main: css({
         padding: '20px 50px'
      }),
      description: css({
         padding: '15px',
         color: 'rgba(255, 255, 255, 0.56)',
         fontSize: '11px',
         fontWeight: '700',
         background: '#2B2455'
      }),

      block: css({
         marginBottom: '28px',
      }),
      title: css({
         marginBottom: '14px',
         fontSize: '15px',
         fontWeight: '700',
      }),
      undertitle: css({
         fontSize: '11px',
         fontWeight: '700',
         marginBottom: '14px'

      }),
      list: css({
         listStyleType: 'disc',
         paddingLeft: '10px'
      }),
      listItem: css({
         color: 'rgba(255, 255, 255, 0.56)',
         fontSize: '11px',
         fontWeight: '700',
         listStyleType: 'disc',
         marginBottom: '14px'
      }),

      footer: css({
         padding: '28px 50px 27px',
         color: 'rgba(255, 255, 255, 0.56)',
         fontSize: '11px',
         fontWeight: '700',
         textAlign: 'center',
         background: '#1F1A3F'
      }),
   };

   const arrayToCreate = [
      {
         title: 'How to play and what are the rules?',
         content: [
            {
               undertitle: '',
               list: [
                  'How to make a bet? You need to select the desired amount and click the "Bet" button.',
                  'At the same time, there is no need to limit yourself to only one bet at a time. You can place two bets at the same time using both the left and right betting panel.',
                  'To withdraw the winnings, you need to click the "Withdrawal" button. Your winnings are made up of the aggregate of your bet multiplied by the cashout multiplier.',
                  'If you do not make a withdrawal before Lucky Joe flies away, then the bet will be lost.'
               ]
            }
         ]
      },
      {
         title: 'Auto bet and Auto Withdrawal',
         content: [
            {
               undertitle: '',
               list: [
                  'An automatic bet can be activated on the panel of any bet if you tick the "Auto bet" line. In this case, bets are placed automatically. Nevertheless, in order to withdraw the winnings, you still need to press the "Withdrawal" button for each round.',
                  'If you want to fully automate the game, then it is possible to configure automatic withdrawal of winnings. To do this, you need to activate the "Auto Withdrawal" on the betting panel. Then the funds will be automatically withdrawn when the coefficient you set is reached.'
               ]
            }
         ]
      },
      {
         title: 'The interface of our game',
         content: [
            {
               undertitle: 'Live Bets and Statistics',
               list: [
                  'On the left (if you use the mobile interface, then under the betting panel) is the "Live Bets" panel. It displays the bets that were made in the current round.',
                  'The "My Bets" panel contains information about bets made and withdrawals for the entire duration of the game.',
                  'The "Top" panel contains game statistics. Here you can study the winnings of other players both in terms of the amount and the cashing coefficient. So you can see the highest coefficients in the round.'
               ]
            },
            {
               undertitle: 'In-game chat',
               list: [
                  'On the right (if you use mobile interface, then in the upper right corner), there is a general chat panel. It is designed to communicate with other players. In addition, information about receiving large winnings is automatically posted in the chat.',
                  'In-game chat prohibits any advertising, such as links to external resources, groups, or channels on social networks or messengers, signals about the expected outcome of rounds, etc. The use of advertising messages as a username is also prohibited. In case of violation, the user may be temporarily or permanently banned.'
               ]
            },
            {
               undertitle: 'In-game chat',
               list: [
                  'On the right (if you use mobile interface, then in the upper right corner), there is a general chat panel. It is designed to communicate with other players. In addition, information about receiving large winnings is automatically posted in the chat.',
                  'In-game chat prohibits any advertising, such as links to external resources, groups, or channels on social networks or messengers, signals about the expected outcome of rounds, etc. The use of advertising messages as a username is also prohibited. In case of violation, the user may be temporarily or permanently banned.'
               ]
            },
            {
               undertitle: 'Working with technical problems',
               list: [
                  'The operator is not responsible for the loss of a bet due to internet connection issues. It is recommended to play with a stable connection.',
                  'If a malfunction occurs on the gaming equipment or software all bets and payouts will be canceled. In this case affected players will be reimbursed their bets in full within 1 hour.'
               ]
            },
         ]
      },
   ]

   const createList = arrayToCreate.map((item, id) => {
      return (
         <div key={id} css={styles.block}>
            <p css={styles.title}>{item.title}</p>
            {item.content.map((contentItem, contentId) => (
               <div key={contentId}>
                  {contentItem.undertitle && (
                     <p css={styles.undertitle}>{contentItem.undertitle}</p>
                  )}
                  <ul css={styles.list}>
                     {contentItem.list.map((listItem, listId) => (
                        <li key={listId} css={styles.listItem}>
                           {listItem}
                        </li>
                     ))}
                  </ul>
               </div>
            ))}
         </div>
      );
   });


   return (

      <Modal icon={rulesLogo} title="RULES OF THE GAME" stylesCustom={styles.modal} headerCustom={styles.headerCustom} isVisible={props.isVisible} closeModal={props.closeModal}>
         <div className="modalDescription">
            <p css={styles.description}>
               Lucky Jet is the latest gambling entertainment that will
               suit a new generation of players. You can win several times
               more in just a few seconds! Lucky Jet is arranged on a
               scheme that can be verified and, at the moment, is
               considered the only really working guarantee of honesty in
               the gambling industry.
            </p>
         </div>
         <div className="modalMain" css={styles.main}>
            {createList}
         </div>
         <div className="modalFooter" css={styles.footer}>
            Play and win
         </div>
      </Modal>

   );
}

export default ModalRules;
