/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";
import { useEffect, useRef, useState } from "react";

function Comments(props) {
    const scrollRef = useRef(null);

    const scrollToBottom = () => {
        if (scrollRef.current) {
            scrollRef.current.scrollTo({
                top: scrollRef.current.scrollHeight,
                behavior: "smooth",
            });
        }
    };

    useEffect(() => {
        scrollToBottom();
    }, []);

    let styles = {
        comments: css({
            padding: "16px 0 27px 0",
            background: props.colors.blocksBg,
            position: "relative",
            maxHeight: "calc(100vh - 30px)",
            display: "grid",
            gridTemplateRows: "auto 1fr",
        }),
        comments_fade: css({
            background:
                "linear-gradient(273deg, #151027 0%, rgba(21, 16, 39, 0) 100%)",
            position: "absolute",
            top: 0,
            left: 0,
            width: "100%",
            height: "124px",
        }),
        comments_list: css({
            paddingLeft: "11px",
            paddingRight: "11px",
            marginBottom: "14px",
            overflow: "hidden",
            overflowY: "auto",
            maxHeight: "calc(100vh - 30px - 100px - 30px - 30px - 30px - 4px)",

            "&::-webkit-scrollbar": {
                display: "none",
            },
            scrollbarWidth: "none",
            msOverflowStyle: "none",
        }),
        comments_add: css({
            paddingTop: "9px",
            borderTop: "rgb(28, 21, 55) 1px solid",
            paddingLeft: "11px",
            paddingRight: "11px",
        }),
        comments_inputcase: css({
            padding: "3px 4px 3px 12px",
            display: "grid",
            gridTemplateColumns: "1fr 31px",
            gridColumnGap: "10px",
            alignItems: "center",
            background: "#010005",
            borderRadius: "6px",
        }),
        comments_input: css({
            display: "block",
            width: "100%",
            border: "none",
            outline: "none",
            alignSelf: "center",
            background: "none",
            color: "#fff",
        }),
        comments_input_btn: css({
            width: "31px",
            height: "31px",
            border: "none",
            outline: "none",
            alignSelf: "center",
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
            background: "#151028",
            borderRadius: "5px",
        }),
        comments_options: css({
            marginTop: "15px",
            display: "flex",
            flexDirection: "row",
            alignItems: "flex-end",
            justifyContent: "space-between",
        }),
        comments_options_btns: css({
            padding: "3px 5px",
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            borderRadius: "7px",
            background: "#0c0918",
        }),
        comments_options_btn_emoji: css({
            padding: "5.8px 6.7px 5.6px",
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
            borderRadius: "7px",
            border: "none",
            outline: "none",
            background: "#231d47",
            minWidth: "26px",
            minHeight: "26px",
        }),
        comments_options_btn_emoji_svg: css({
            marginTop: "1px",
            width: "10px",
        }),
        comments_options_btn_gif: css({
            padding: "5.8px 6.7px 5.6px",
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
            fontWeight: "600",
            fontSize: "11px",
            color: "#8e87b3",
            minWidth: "26px",
            minHeight: "26px",
            borderRadius: "7px",
            border: "none",
            outline: "none",
            background: "#231d47",
        }),
        comments_options_divider: css({
            marginLeft: "3.5px",
            marginRight: "3.5px",
            width: "2px",
            height: "15px",
            background: "#231d47",
        }),
        comments_options_max: css({
            fontWeight: 400,
            fontSize: "8px",
            color: "#666b81",
        }),
        item_normal: css({
            display: "grid",
            gridTemplateColumns: "auto auto 1fr",
            marginBottom: "9px",
        }),
        item_long_text: css({
            display: "grid",
            gridTemplateColumns: "26px auto",
            marginBottom: "5px",
        }),
        item_name: css({
            marginLeft: "6px",
            paddingTop: "7px",
            fontWeight: 600,
            fontSize: "12px",
            color: "#9287c2",
        }),
        item_text: css({
            marginLeft: "4px",
            paddingTop: "7.5px",
            fontWeight: 600,
            fontSize: "11px",
            color: "#fff",
            
        }),
        item_text_plus: css({
            marginLeft: "6px",
            paddingTop: "3px",
            gridColumn: 2,
            fontSize: '11px'
        }),
    };

    const [commentsArray, setCommentsArray] = useState([
        {
            name: "SLAY",
            text: "insane",
        },
        {
            name: "",
            text: "   ",
        },
        {
            name: "Broski",
            text: " ",
        },
        {
            name: "Broski",
            text: "  ?",
        },
        {
            name: "MaNIaK",
            text: " 50",
        },
        {
            name: "SLAY",
            text: "insane",
        },
        {
            name: "",
            text: "   ",
        },
        {
            name: "Broski",
            text: " ",
        },
        {
            name: "Broski",
            text: "  ?",
        },
        {
            name: "MaNIaK",
            text: " 50",
        },
        {
            name: "Broski",
            text: "",
        },
        {
            name: "",
            text: "   ,   .       ",
        },
        {
            name: "28255791",
            text: "Marco dinero",
        },
        {
            name: "",
            text: "",
        },
        {
            name: "Elina",
            text: " 2",
        },
        {
            name: "",
            text: "  15",
        },
        {
            name: "",
            text: "  ",
        },
        {
            name: "",
            text: "   ",
        },
        {
            name: "Anni",
            text: "",
        },
        {
            name: "-",
            text: "8    40 ..     ",
        },
        {
            name: "",
            text: "",
        },
        {
            name: "Elina",
            text: " 2",
        },
        {
            name: "",
            text: "  15",
        },
        {
            name: "",
            text: "  ",
        },
        {
            name: "",
            text: "   ",
        },
        {
            name: "Anni",
            text: "",
        },
        {
            name: "-",
            text: "8    40 ..     ",
        },
    ]);

    let createCommnets = commentsArray.map((item, id) => {
        let name = item.name;
        let text = item.text;

        let imgText = name.slice(0, 2).toUpperCase();

        let styleImg = css({
            background: `#${
                props.styling.bgColorsArray[props.styling.randomizeBgColor()]
            }`,
            width: "26px",
            height: "26px",
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
            fontSize: "12px",
            paddingTop: "1px",
            lineHeight: "100%",
            borderRadius: "50%",
            gridRow: "1/3",
        });

        let styleText = styles.item_text;

        let itemStyleCheck = () => {
            if (text.length > 25) {
                styleText = { ...styles.item_text, ...styles.item_text_plus };
                return styles.item_long_text;
            } else {
                return styles.item_normal;
            }
        };
        // itemStyleCheck();
        return (
            <div
                className="Comments__item"
                css={itemStyleCheck()}
                key={`${id}-${name}`}
            >
                <div css={styleImg}>
                    <span>{imgText}</span>
                </div>
                <p css={styles.item_name}>{name}:</p>
                <p css={styleText}>{text}</p>
            </div>
        );
    });

    return (
        <div className="Comments" css={styles.comments} >
            <div className="Comments__fade" css={styles.comments_fade}></div>

            <div className="Comments__list" css={styles.comments_list} ref={scrollRef}>
                {createCommnets}
            </div>

            <div className="Comments__add" css={styles.comments_add}>
                <form
                    className="Comments__input-case"
                    css={styles.comments_inputcase}
                >
                    <input
                        type="text"
                        placeholder="Enter a message"
                        css={styles.comments_input}
                    />
                    <button
                        className=""
                        type="button"
                        css={styles.comments_input_btn}
                        onClick={props.showRegistration}
                    >
                        <svg
                            width="15"
                            height="13"
                            viewBox="0 0 15 13"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                        >
                            <path
                                d="M6.59794 6.50053H2.82192M2.7638 6.68764L1.16077 11.1647C1.03483 11.5164 0.971865 11.6923 1.01705 11.8006C1.0563 11.8946 1.14058 11.9659 1.24458 11.9931C1.36434 12.0243 1.54522 11.9482 1.90699 11.796L13.3802 6.96882C13.7333 6.82025 13.9099 6.74597 13.9644 6.64278C14.0119 6.55313 14.0119 6.44793 13.9644 6.35828C13.9099 6.25509 13.7333 6.18081 13.3802 6.03224L1.90299 1.20339C1.54231 1.05164 1.36197 0.975765 1.24233 1.00689C1.13843 1.03392 1.05416 1.10503 1.01478 1.19891C0.969437 1.307 1.03173 1.48248 1.15632 1.83344L2.76425 6.36286C2.78565 6.42314 2.79635 6.45328 2.80057 6.4841C2.80432 6.51145 2.80428 6.53914 2.80046 6.56649C2.79615 6.5973 2.78537 6.62742 2.7638 6.68764Z"
                                stroke="#8E87B3"
                                strokeWidth="2"
                                strokeLinecap="round"
                                strokeLinejoin="round"
                            />
                        </svg>
                    </button>
                </form>
                <div
                    className="Comments__options"
                    css={styles.comments_options}
                >
                    <div
                        className="Comments__options-btns"
                        css={styles.comments_options_btns}
                    >
                        <button css={styles.comments_options_btn_emoji}>
                            <svg
                                css={styles.comments_options_btn_emoji_svg}
                                width="14"
                                height="12"
                                viewBox="0 0 14 12"
                                fill="none"
                                xmlns="http://www.w3.org/2000/svg"
                            >
                                <circle
                                    cx="3.0664"
                                    cy="2.0664"
                                    r="2.0664"
                                    fill="#8E87B3"
                                />
                                <circle
                                    cx="11.332"
                                    cy="2.0664"
                                    r="2.0664"
                                    fill="#8E87B3"
                                />
                                <path
                                    d="M1.68884 8.26562C3.52565 10.332 8.36719 13.0872 12.5 8.26562"
                                    stroke="#8E87B3"
                                    strokeWidth="2"
                                />
                            </svg>
                        </button>

                        <span css={styles.comments_options_divider}></span>

                        <button css={styles.comments_options_btn_gif}>
                            GIF
                        </button>
                    </div>

                    <p css={styles.comments_options_max}>max 160 characters</p>
                </div>
            </div>
        </div>
    );
}

export default Comments;
