/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';

import { useEffect, useState } from "react";
import Jet from "./Jet";
import JetLoading from "./JetLoading";

function JetMain(props) {
    const [isJetVisible, setIsJetVisible] = useState(false);
    const [isLoadingVisible, setIsLoadingVisible] = useState(true);

    function showJet(bool){
        setIsJetVisible(bool)
    }
    function showLoading(bool){
        setIsLoadingVisible(bool)
    }

    function getRandomNumberInRange(min = 1.00, max = 10.00) {
        if (min < 1.00) {
            min = 1.00;
        }
        const randomNum = Math.random() * (max - min) + min;
        return parseFloat(randomNum.toFixed(5));
    }

    const [jetCounterTo, setJetCounterTo] = useState(getRandomNumberInRange(1.00, 2))
    
    useEffect(()=>{
        setJetCounterTo(getRandomNumberInRange(1.00, 5))
    },[isJetVisible])

    const styles = {
        jetMain: css({
            margin: '20px 10px 0px',
            // width: 'calc(100vw - (10px * 2))',
            width: 'calc(100vw - (20px* 2))',
            height: 'calc(53vw - (10px * 2))',
            borderRadius: '15px',
            border: '0.25px solid #7862C9',
            overflow: 'hidden',

            "@media (min-width: 960px)": {
                width: '100%',
                height: '100%',
                margin: 'unset',
                border: 'unset',
                borderRadius: 'unset',
                borderBottom: '0.25px solid #7862C9'

            },
        }),
        
    };
    
    return (
        <div className="JetMain" css={styles.jetMain}>
            {/* <Jet jetEnd={jetEnd} /> */}
            {/* <JetLoading /> */}


            {isJetVisible && <Jet jetCounterTo={jetCounterTo}  showJet={showJet} showLoading={showLoading} addCoef={props.addCoef}/>}

            {isLoadingVisible && <JetLoading jetCounterTo={jetCounterTo} showJet={showJet} showLoading={showLoading}/>}
            {/* <JetLoading jetCounterTo={jetCounterTo} showJet={showJet} showLoading={showLoading}/> */}
        </div>
    );
}

export default JetMain;
