/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";
import logoImg from "../assets/images/logo.svg";

function Logo(props) {
    const styles = {
        logo: css({
            background: props.colors.blocksBg,
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
            maxWidth: "116px",
            "@media (min-width: 960px)": {
                maxWidth: "100%",
            },
        }),
        logo__img: css({
            width: "100%",
            "@media (min-width: 960px)": {
                width: "unset",
            },
        }),
    };

    return (
        <div className="Logo" css={styles.logo}>
            <img src={logoImg} css={styles.logo__img} alt="Logo" />
        </div>
    );
}

export default Logo;
