import React, { useEffect, useState, forwardRef } from "react";
import SVGSATART from "./SVGSATART";
import JetMan from "./JetMan";
import FlyText from "./FlyText";

const JetFly = forwardRef((props, ref) => {
    const [trustObj, setTrustObj] = useState({
        width: 0,
        height: 0,
        svgWidthPer: 66.6,
        svgWidthCurve: 50,
        svgHeightPer: 40,
    });

    const [pathLine, setPathLine] = useState("");
    const [pathUnderline, setPathUnderline] = useState("");

    const [objForMan, setObjForMan] = useState({
        top: 0,
        left: 0,
        bottom: 0,
    });

    const [jetManWidth, setJetManWidth] = useState(200);
    const [jetManCounted, setJetManCounted] = useState(false);

    // update objForMan
    function changeObjForMan(value) {
        setObjForMan(value);
    }

    // Effect to update trustObj 
    useEffect(() => {
        const element = document.getElementById("jetCont");
        if (element) {
            const updatedTrustObj = {
                width: props.deliverObject.width,
                height: props.deliverObject.height,
                svgWidthPer: trustObj.svgWidthPer,
                svgWidthCurve: trustObj.svgWidthCurve,
                svgHeightPer: trustObj.svgHeightPer,
            };
            setTrustObj(updatedTrustObj);
        }
    }, [props.deliverObject]);

    // trigger counting end
    function jetManCountedToFinal() {
        setJetManCounted(true);
    }

    // trigger counting reset
    function jetManCountedReset() {
        setJetManCounted(false);
    }

    return (
        <div className="JetFly" id="JetFly" ref={ref}>
            <JetMan 
                objForMan={objForMan} 
                jetManCounted={jetManCounted} 
            />
            <FlyText 
                jetCounterTo={props.jetCounterTo} 
                jetManWidth={jetManWidth} 
                jetEnd={props.jetEnd} 
                jetManCountedToFinal={jetManCountedToFinal} 
                jetManCountedReset={jetManCountedReset} 
                showJet={props.showJet} 
                showLoading={props.showLoading} 
                addCoef={props.addCoef} 
            />
            <SVGSATART
                trustObj={trustObj}
                pathLine={pathLine}
                setPathLine={setPathLine}
                pathUnderline={pathUnderline}
                setPathUnderline={setPathUnderline}
                objForMan={objForMan}
                changeObjForMan={changeObjForMan}
                jetManCounted={jetManCounted} 
            />
        </div>
    );
});

export default JetFly;