import React, { useEffect, useState } from 'react';

function SVGSATART(props) {
    const {
        trustObj,
        height,
        pathLine,
        setPathLine,
        pathUnderline,
        setPathUnderline,
    } = props;

    const updatePaths = (updatedTrustObj) => {
        const x1 = updatedTrustObj.width * (updatedTrustObj.svgWidthCurve / 100);
        const y1 = trustObj.height; 
        const x2 = updatedTrustObj.width * (updatedTrustObj.svgWidthPer / 100); 
        const y2 = trustObj.height * (updatedTrustObj.svgHeightPer / 100);

        if (!isNaN(y1) && !isNaN(x1) && !isNaN(x2) && !isNaN(y2)) {
            setPathLine(`M 0 ${y1} Q ${x1} ${y1} ${x2} ${y2}`);
            setPathUnderline(`M 0 ${y1} Q ${x1} ${y1} ${x2} ${y2} L ${x2} ${y1}`);
        }
    };

    const [multiplyer, setMultiplyer] = useState(0) 
    const [timePathChange, setTimePathChange] = useState(100)

    const [isPathsFreezed, setIsPathsFreezed] = useState(1)

    useEffect(() => {
        if (multiplyer === 100) {
            updatePaths(trustObj); 

            const intervalId = setInterval(() => {
                
                const getRandomChange = () => {
                    return Math.floor(Math.random() * (10 - 5 + 1)) + 5; 
                };

                let newTrustObj = {
                    ...trustObj,
                    svgWidthCurve: Math.min(trustObj.svgWidthCurve + getRandomChange() * multiplyer / 100, 100),
                    svgWidthPer: Math.min(trustObj.svgWidthPer + getRandomChange() * multiplyer / 100, 100),
                    svgHeightPer: Math.min(trustObj.svgHeightPer + getRandomChange() * multiplyer / 100, 100) - 10,
                };

                
                props.changeObjForMan({
                    left: newTrustObj.width * (newTrustObj.svgWidthPer / 100) + 20, 
                    bottom: trustObj.height - newTrustObj.height * (newTrustObj.svgHeightPer / 100) - 100, 
                });

                

                updatePaths(newTrustObj);
            }, timePathChange); 

            return () => clearInterval(intervalId); 
        }
    }, [trustObj, height, setPathLine, setPathUnderline, timePathChange, multiplyer]) ; 


    useEffect(()=>{
        if(props.jetManCounted){
            setIsPathsFreezed(100000)
        }
    },[props.jetManCounted])

    useEffect(() => {
        if (multiplyer === 0) {
            props.changeObjForMan({
                left: 20,
                bottom: 20, 
            });
        }
    
        if (multiplyer < 100) {
            const multInterval = setInterval(() => {
                setMultiplyer(prev => Math.min(prev + 10, 100)); 
    
                let newTrustObj = {
                    ...trustObj,
                    svgWidthCurve: Math.min(trustObj.svgWidthCurve, 100) * (multiplyer / 100),
                    svgWidthPer: Math.min(trustObj.svgWidthPer, 100) * (multiplyer / 100),
                    svgHeightPer: Math.min(trustObj.svgHeightPer, 100) * (230 - (multiplyer * 100 / 230)) / 100,
                };
    
                props.changeObjForMan({
                    left: trustObj.width * newTrustObj.svgWidthPer / 100 + 40, 
                    bottom: (newTrustObj.height - newTrustObj.height * (58 / 100)) / 100 * multiplyer - 50,
                });
    
                updatePaths(newTrustObj);
            }, 50);
            return () => clearInterval(multInterval);
        } else {
            setTimePathChange(600); 
        }
    }, [trustObj, multiplyer]);

    

    return (
        <div className='SvgStart'
            style={{
                width: "100%",
                height: "100%",
                position: "absolute",
                bottom: "0px",  
                left: "0px",
                zIndex: 1
            }}>
            <svg
                style={{
                    width: "100%",
                    height: "100%",
                    position: "absolute",
                    bottom: "0px",  
                    left: "0px",
                }}>
                <defs>
                    <linearGradient id="grad" x1="0" x2="1" y1="0" y2="1">
                        <stop stopColor="#9d7aff" stopOpacity=".33"></stop>
                        <stop offset=".987" stopColor="#9d7aff" stopOpacity="0"></stop>
                    </linearGradient>
                    <linearGradient
                        id="grad_stroke"
                        x1="0"
                        x2="1"
                        y1="0"
                        y2="1">
                        <stop stopColor="#9D7AFF"></stop>
                        <stop offset=".787" stopColor="#622BFC"></stop>
                        <stop offset="1" stopColor="#5c24fc" stopOpacity="0"></stop>
                    </linearGradient>
                </defs>  
                <g>
                    <path
                        style={{ transition: `all ${isPathsFreezed}s ease-out` }}
                        d={pathLine}
                        fill="transparent"
                        stroke={"url(#grad_stroke)"}
                    />
                    <path 
                        style={{ transition: `all ${isPathsFreezed}s ease-out` }}
                        d={pathUnderline} 
                        fill={"url(#grad)"}
                    />
                </g>
            </svg>
        </div>
    );
}

export default SVGSATART;