/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";
import man from "../assets/images/jetMan.webp";
import flame from "../assets/images/man-flame.svg";
import flameBlur from "../assets/images/man-flame-blur.webp";
import { useEffect, useState } from "react";

function JetMan(props) {
    const [bottom, setBottom] = useState(`${props.objForMan.bottom}px`);
    const [left, setLeft] = useState(`${props.objForMan.left}px`);

    // jetman counted from percents and at start used block with equal width and height
    const [jetmanSide, setJetmanSide] = useState(100);

    const checkJetSide = () => {
        if (jetmanSide >= 200) {
            return 0;
        } 
        if (jetmanSide >= 150) {
            return jetmanSide / 4;
        } 
        if (jetmanSide >= 100){
            return jetmanSide / 2;
        }
    };

    useEffect(() => {
        if (window.innerWidth > 960) {
            setJetmanSide(200);
        }
        if (window.innerWidth > 720 && window.innerWidth < 960) {
            setJetmanSide(180);
        }
        if (window.innerWidth > 640 && window.innerWidth < 720) {
            setJetmanSide(160);
        }
        if (window.innerWidth > 500 && window.innerWidth < 640) {
            setJetmanSide(140);
        }
        if (window.innerWidth < 500) {
            setJetmanSide(100);
        }

        checkJetSide();
    }, [window.innerWidth]);

    useEffect(() => {
        if (props.jetManCounted) {
            setBottom(`${props.objForMan.bottom * 3 + checkJetSide()}px`);
            setLeft(`${props.objForMan.left * 2}px`);
        } else {
            setBottom(`${props.objForMan.bottom + checkJetSide()}px`);
            setLeft(`${props.objForMan.left}px`);
        }
    }, [props.jetManCounted, props.objForMan.bottom, props.objForMan.left]);

    const styles = {
        jetMan: css({
            zIndex: 10,
            position: "absolute",
            display: "grid",
            placeItems: "center",
            width: "100%",
            maxWidth: `${jetmanSide}px`,
            height: `${jetmanSide}px`,
            bottom: bottom,
            left: left,
            transition: "all 1s ease-out",
            transform: "translateX(-50%)",

            "@media only screen and (min-width: 960px)": {
                maxWidth: `${jetmanSide}px`,
                height: `${jetmanSide}px`,
            },
        }),
        man: css({
            position: "absolute",
            transition: "0.32s",
            width: "50%",
        }),
        flame: css({
            position: "absolute",
            width: "24%",
            marginTop: "62%",
            marginLeft: "-64%",
            transform: "rotate(-138deg)",
            transformOrigin: "center center",
        }),
        flameBlur: css({
            position: "absolute",
            width: "50%",
            marginTop: "45%",
            marginLeft: "-50%",
            transform: "rotate(36deg)",
        }),
    };

    return (
        <div className="JetMan" css={styles.jetMan}>
            <img src={man} css={styles.man} alt="jetman" />
            <img src={flame} css={styles.flame} alt="flame" />
            <img src={flameBlur} css={styles.flameBlur} alt="flame-blur" />
        </div>
    );
}

export default JetMan;
