/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

import { useEffect, useState } from "react";


import modalCross from "../assets/images/modalCross.svg";




function Modal(props) {



    const styles = {
        modal: css({
            position: 'absolute',
            top: 0,
            left: 0,
            maxWidth: '360px',
            zIndex: 11,
            background: '#231D47',
            borderRadius: '12px',
        }),
        header: css({
            display: 'grid',
            gridTemplateColumns: 'auto 1fr auto',
            background: '#1F1A3F',
            padding: '11px',
            borderRadius: '12px 12px 0 0',
            alignItems: 'center',
        }),
        title: css({
            marginLeft: '15px',
            fontSize: '16px',
            fontStyle: 'normal',
            fontWeight: '700',
        }),
        close: css({
            width: '27px',
            height: '27px',
            borderRadius: '9px',
            background: '#2F201C',
            border: '2px solid #110D21'
        }),
        //   modal: css({}),
        //   modal: css({}),
    };



    return (

        <>
            {props.isVisible && (
                <div className="Modal" css={[
                    styles.modal,
                    css(props.stylesCustom),
                ]}>
                    <div className="Modal__header" css={[
                        styles.header,
                        css(props.headerCustom),
                    ]}>
                        <img src={props.icon} alt="" />
                        <p css={styles.title}>{props.title}</p>
                        <button css={styles.close} onClick={props.closeModal}>
                            <img src={modalCross} alt="close modal" />
                        </button>
                    </div>
                    <div className="Modal__body">
                        {props.children}
                    </div>
                </div>
            )}

        </>

    );
}

export default Modal;
