/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

import { useState, useEffect } from "react";

function FlyText(props) {
    const [stopCountOn] = useState(props.jetCounterTo);
    const [count, setCount] = useState(1.0);

    useEffect(() => {
        const interval = setInterval(() => {
            setCount((prevCount) => {
                const newCount = prevCount + 0.01;

                if (newCount >= stopCountOn) {
                    clearInterval(interval);
                    setTimeout(() => {
                        props.jetManCountedToFinal();

                        setTimeout(() => {
                            props.showJet(false);
                            props.showLoading(true);
                            props.addCoef(count.toFixed(2));
                            props.jetManCountedReset();
                        }, 500);
                    }, 10);
                    return stopCountOn;
                }

                return newCount;
            });
        }, 100);

        return () => clearInterval(interval);
    }, [stopCountOn, props]);

    const styles = {
        flyText: css({
            zIndex: 10,
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
        }),
        counterText: css({
            fontWeight: "900",
            // fontSize: "65px",
            color: "#944ef5",

            fontSize: "25px",
            textAlign: "center",
            display: "block",

            "@media only screen and (min-width: 960px)": {
                fontSize: "65px",
            },
            
            "@media only screen and (min-width: 700px) and (max-width: 960px)": {
                fontSize: "46px",
            },
            "@media only screen and (min-width: 500px) and (max-width: 700px)": {
                fontSize: "34px",
            },
            
            
        }),
        flewText: css({
            fontFamily: "Rocketfont",
            fontStyle: "normal",
            fontWeight: "400",
            lineHeight: "25px",
            letterSpacing: "0px",
            textTransform: "uppercase",
            color: "rgb(250, 250, 250)",
            textShadow: "rgba(138, 138, 138, 0.5) 0px 0px 23x",
            opacity: count >= stopCountOn ? 1 : 0,
            height: "25px",

            fontSize: "25px",
            "@media only screen and (min-width: 960px)": {
                fontSize: "34px",
                height: "45px",
            },
            "@media only screen and (min-width: 700px) and (max-width: 960px)": {
                fontSize: "24px",
                height: "24px",
            },
            "@media only screen and (min-width: 500px) and (max-width: 700px)": {
                fontSize: "18px",
                height: "18px",
            },
        }),
    };

    return (
        <div className="FlyText" css={styles.flyText}>
            <span css={styles.counterText}>x {count.toFixed(2)}</span>
            <p className="" css={styles.flewText}>
                Flew Away
            </p>
        </div>
    );
}

export default FlyText;
