import React, { forwardRef } from "react";
import JetFly from "./JetFly";

const JetContent = forwardRef((props, ref) => {
    return (
        <div
            className="JetContent"
            style={{
                position: "relative",
                width: "100%",
                height: "100%",
            }}
            id="jetCont"
            ref={ref}
        >
            <JetFly
                jetCounterTo={props.jetCounterTo}
                showJet={props.showJet}
                showLoading={props.showLoading}
                addCoef={props.addCoef}
                deliverObject={props.deliverObject}
            />
        </div>
    );
});

export default JetContent;