/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

import { useState } from "react";

function Bets(props) {
    let styles = {
        bets: css({
            padding: "10px 12px 16px 12px",
            display: "grid",
            gridTemplateColumns: "1fr 1fr",
            gridColumnGap: "12px",

            "@media only screen and (max-width: 960px)": {
                gridTemplateColumns: "1fr",
                gridTemplateRows: "auto auto",
                gridGap: "20px",
            },
        }),
        item: css({
            background: "linear-gradient(90deg, #1e183b 0%, #2d255b 100%)",
            borderRadius: "13px",
        }),
        head: css({
            display: "grid",
            gridTemplateColumns: "repeat(3, auto)",
            padding: "15px 10px 12px",
        }),
        check: css({
            display: "flex",
            flexDirection: "row",
            alignItems: "center",

            fontSize: '13px',
            "@media (max-width: 960px)": {
                fontSize: '10px',
            },
        }),
        check_input: css({
            marginRight: "8px",
            minWidth: "27px",
            minHeight: "27px",
            border: "3px solid #151028",
            borderRadius: "7px",
            

            "@media (max-width: 960px)": {
                minWidth: "22px",
                minHeight: "22px",
            marginRight: "4px",

            },
        }),
        coef: css({
            position: "relative",
            maxWidth: "120px",
            justifySelf: "end",
            display: "flex",
            flexDirection: "row",
            alignContent: "center",
            justifyContent: "space-around",
            background: "#151028",
            borderRadius: "7px",

            "@media (max-width: 960px)": {
                maxWidth: "100px",

            },
        }),
        coef_wrapper: css({
            position: "absolute",
            left: "0px",
            top: "0px",
            width: "100%",
            height: "100%",
            display: "grid",
            justifyContent: "center",
            alignItems: "center",
            gridAutoColumns: "max-content",
            gridAutoFlow: "column",
            fontStyle: "normal",
            fontWeight: "bold",
            fontSize: "13px",
            lineHeight: "13px",
            textAlign: "center",
            gap: "4px",
            color: "transparent",
            pointerEvents: "none",
            opacity: "1",
            transition: "opacity 0.16s ease 0s",
        }),
        coef_pred: css({
            color: "rgb(148, 138, 197)",
        }),
        coef_value: css({
            fontStyle: "normal",
            fontWeight: "bold",
            fontSize: "13px",
            lineHeight: "13px",
            textAlign: "center",
            color: "transparent",
            pointerEvents: "none",
        }),
        coef_input: css({
            display: "block",
            maxWidth: "100%",
            fontSize: "13px",
            fontStyle: "normal",
            fontWeight: "700",
            lineHeight: "13px",
            letterSpacing: "0px",
            background: "none",
            border: "none",
            outline: "none",
            width: "100%",
            height: "100%",
            textAlign: "center",
            paddingLeft: "12px",
            opacity: "1",
            transition: "opacity 0.16s ease 0s",
            boxShadow: "transparent 0px 0px 0px 1000px inset",
            color: "#fff",
        }),
        item_body: css({
            padding: "16px 10px 11px",
            display: "grid",
            gridTemplateColumns: "1fr 1fr",
            gridColumnGap: "12px",
            borderRadius: "13px",
            background: "#251e48",
        }),
        options: css({
            background: "#151028",
            borderRadius: "11px",
        }),
        options_head: css({
            padding: "7px",
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
            alignItems: "center",
        }),
        options_head_btn: css({
            borderRadius: "9px",
            minWidth: "27px",
            minHeight: "27px",
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
            border: "none",
            background: "#231d47",
            "@media (max-width: 960px)": {
                minWidth: "22px",
            minHeight: "22px",
            },
        }),
        bet_options_wrapper: css({
            position: "relative",
        }),
        bet_input: css({
            background: "none",
            textAlign: "center",
            fontWeight: "900",
            fontSize: "14px",
            color: "#fff",
            outline: "none",
            border: "none",
            fontFamily: "SF Pro Display",
            width: '100%',

            "@media (max-width: 960px)": {
                fontSize: "11px",
            },
        }),
        bet_wrapper: css({
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            pointerEvents: "none",
        }),
        bet_wrapper_value: css({
            color: "transparent",
            fontWeight: "900",
            fontSize: "14px",
            paddingRight: "3px",
        }),
        bet_wrapper_post: css({
            display: "inline-block",
            position: "absolute",
            top: "2px",
            right: "-10px",
            textAlign: "left",
            paddingBottom: "1px",
            fontWeight: "900",
            fontSize: "14px",
            color: "#fff",


            "@media (max-width: 960px)": {
                top: "6px",
                fontSize: '11px',
                right: "-8px",

            },
        }),
        options_body: css({
            padding: "4px 6px 4px",
            border: "1px solid #251e48",
            display: "grid",
            gridTemplateColumns: "1fr 1fr 1fr 1fr",
            gridColumnGap: "2px",
        }),
        options_body_btn: css({
            padding: "6px 5px 6px",
            borderRadius: "9px",
            border: "none",
            background: "#231d47",
            fontWeight: "700",
            fontSize: "10px",
            color: "#948ac5",
            textAlign: "center",
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",

            "@media (max-width: 960px)": {
                fontSize: "8px",
            },
        }),
        bets_btn: css({
            padding: "24px 20px 20px 20px",
            background:
                "radial-gradient(630.78% 70.21% at 9.74% 26.67%, rgb(97, 40, 252) 0%, rgb(142, 73, 246) 100%)",
            borderRadius: "11px",
            border: "4px",
        }),
    };

    const [coef1, setCoef1] = useState("2.0");
    const [coef2, setCoef2] = useState("2.0");

    const handleCoef1Change = (event) => {
        let result = Number(event.target.value);
        if (result > 999.99) {
            return;
        } else {
            if (result === coef1) {
                setCoef1(event.target.value);
            } else {
                let arr = result.toString().split(".");

                if (arr.length !== 1) {
                    if (arr[1].length > 2) {
                        return;
                    } else {
                        setCoef1(result);
                    }
                } else {
                    setCoef1(event.target.value);
                }
            }
        }
    };
    const handleCoef2Change = (event) => {
        let result = Number(event.target.value);
        if (result > 999.99) {
            return;
        } else {
            if (result === coef1) {
                setCoef2(event.target.value);
            } else {
                let arr = result.toString().split(".");

                if (arr.length !== 1) {
                    if (arr[1].length > 2) {
                        return;
                    } else {
                        setCoef2(result);
                    }
                } else {
                    setCoef2(event.target.value);
                }
            }
        }
    };

    const [bet1, setBet1] = useState("0.2");
    const [bet2, setBet2] = useState("0.2");

    const handleBet1Add = (value) => {
        let result = Number(bet1) + value;
        if (result >= 0.1 && result <= 999.9) {
            setBet1(result.toFixed(1));
        }
        
    };
    const handleBet2Add = (value) => {
        let result = Number(bet2) + value;
        if (result >= 0.1 && result <= 999.9) {
            setBet2(result.toFixed(1));
        }
    };

    const handleBet1Change = (event) => {
        let value = event.target.value;

        if (value > 999.99) {
            return;
        } else {
            let arr = value.toString().split(".");
            if (arr.length === 1) {
                setBet1(value);
            } else {
                if (arr[1].length > 2) {
                    return;
                } else {
                    setBet1(value);
                }
            }
        }
    };
    const handleBet2Change = (event) => {
        let value = event.target.value;

        if (value > 999.99) {
            return;
        } else {
            let arr = value.toString().split(".");
            if (arr.length === 1) {
                setBet2(value);
            } else {
                if (arr[1].length > 2) {
                    return;
                } else {
                    setBet2(value);
                }
            }
        }
    };

    const handleBlur = (value, setValue) => {
        if (value < 0.1) {
            setValue(0.1);
        }
    };

    return (
        <div className="Bets" css={styles.bets}>
            <div className="Bets__item" css={styles.item}>
                <div className="Bets__item__head" css={styles.head}>
                    <div className="Bets__check" css={styles.check}>
                        <div
                            className="Bets__check-input"
                            css={styles.check_input}
                        ></div>
                        <span className="Bets__check-name">Auto bet</span>
                    </div>
                    <div className="Bets__check" css={styles.check}>
                        <div
                            className="Bets__check-input"
                            css={styles.check_input}
                        ></div>
                        <span className="Bets__check-name">
                            Auto withdrawal
                        </span>
                    </div>

                    <div className="Bets__coef" css={styles.coef}>
                        <div
                            className="Bets__coef-wrapper"
                            css={styles.coef_wrapper}
                        >
                            <div
                                className="Bets__coef-pred"
                                css={styles.coef_pred}
                            >
                                x
                            </div>
                            <div
                                className="Bets__coef-value"
                                css={styles.coef_value}
                            >
                                {coef1}
                            </div>
                        </div>

                        <input
                            type="text"
                            css={styles.coef_input}
                            value={coef1}
                            onChange={handleCoef1Change}
                            onBlur={() => {
                                handleBlur(coef1, setCoef1);
                            }}
                        />
                    </div>
                </div>

                <div className="Bets__item__body" css={styles.item_body}>
                    <div className="Bets__options" css={styles.options}>
                        <div
                            className="Bets__option_head"
                            css={styles.options_head}
                        >
                            <button
                                className="Bets__option_minus"
                                css={styles.options_head_btn}
                                onClick={() => {
                                    handleBet1Add(-0.1);
                                }}
                            >
                                <svg
                                    width="10"
                                    height="2"
                                    viewBox="0 0 10 2"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                >
                                    <path d="M10 2H0V0H10V2Z" fill="#948AC5" />
                                </svg>
                            </button>
                            <div
                                className="Bets__options-value-wrapper"
                                css={styles.bet_options_wrapper}
                            >
                                <input
                                    type="text"
                                    value={bet1}
                                    onChange={handleBet1Change}
                                    css={styles.bet_input}
                                    onBlur={() => {
                                        handleBlur(bet1, setBet1);
                                    }}
                                />

                                <div
                                    className="Bets__options-wrapper"
                                    css={styles.bet_wrapper}
                                >
                                    <span
                                        className="Bets__options-value-"
                                        css={styles.bet_wrapper_value}
                                    >
                                        {bet1}
                                    </span>

                                    <span
                                        className="Bets__options-post"
                                        css={styles.bet_wrapper_post}
                                    >
                                        $
                                    </span>
                                </div>
                            </div>
                            <button
                                className="Bets__option_minus"
                                css={styles.options_head_btn}
                                onClick={() => {
                                    handleBet1Add(0.1);
                                }}
                            >
                                <svg
                                    width="13"
                                    height="13"
                                    viewBox="0 0 13 13"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                >
                                    <path
                                        d="M7.24661 7.4694V12.7578H4.92488V7.4694H0V5.18285H4.92488V0H7.24661V5.18285H12.1715V7.4694H7.24661Z"
                                        fill="#948AC5"
                                    />
                                </svg>
                            </button>
                        </div>
                        <div
                            className="Bets__option_body"
                            css={styles.options_body}
                        >
                            <button
                                className="Bets__option_body-plus "
                                css={styles.options_body_btn}
                                onClick={() => {
                                    handleBet1Add(50);
                                }}
                            >
                                +50
                            </button>
                            <button
                                className="Bets__option_body-plus"
                                css={styles.options_body_btn}
                                onClick={() => {
                                    handleBet1Add(100);
                                }}
                            >
                                +100
                            </button>
                            <button
                                className="Bets__option_body-plus"
                                css={styles.options_body_btn}
                                onClick={() => {
                                    handleBet1Add(200);
                                }}
                            >
                                +200
                            </button>
                            <button
                                className="Bets__option_body-plus"
                                css={styles.options_body_btn}
                                onClick={() => {
                                    handleBet1Add(500);
                                }}
                            >
                                +500
                            </button>
                        </div>
                    </div>
                    <button className="Bets__btn" css={styles.bets_btn} onClick={props.showRegistration}>
                        <svg
                            width="39"
                            height="12"
                            viewBox="0 0 39 12"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                        >
                            <path
                                d="M34.7392 11.614H30.821V2.38235H26.5602V0H39V2.38235H34.7392V11.614Z"
                                fill="white"
                            />
                            <path
                                d="M25.0187 9.23162V11.614H14.431V0H25.0187V2.38235H18.3599V4.69227H24.6226V6.86536H18.3599V9.23162H25.0187Z"
                                fill="white"
                            />
                            <path
                                d="M7.37607 11.614H0V0H7.18337C10.2237 0 12.0544 1.10264 12.0544 2.89746C12.0544 4.20131 10.7483 5.23957 9.03541 5.41664V5.48102C11.123 5.55346 12.7716 6.72049 12.7716 8.2658C12.7716 10.3021 10.6841 11.614 7.37607 11.614ZM3.92891 2.09261V4.73251H5.95224C7.37607 4.73251 8.2325 4.22546 8.2325 3.38842C8.2325 2.57552 7.46171 2.09261 6.14494 2.09261H3.92891ZM3.92891 9.52137H6.2627C7.87922 9.52137 8.77848 8.99017 8.77848 8.00825C8.77848 7.05853 7.85781 6.52733 6.24129 6.52733H3.92891V9.52137Z"
                                fill="white"
                            />
                        </svg>
                    </button>
                </div>
            </div>
            <div className="Bets__item" css={styles.item}>
                <div className="Bets__item__head" css={styles.head}>
                    <div className="Bets__check" css={styles.check}>
                        <div
                            className="Bets__check-input"
                            css={styles.check_input}
                        ></div>
                        <span className="Bets__check-name">Auto bet</span>
                    </div>
                    <div className="Bets__check" css={styles.check}>
                        <div
                            className="Bets__check-input"
                            css={styles.check_input}
                        ></div>
                        <span className="Bets__check-name">
                            Auto withdrawal
                        </span>
                    </div>

                    <div className="Bets__coef" css={styles.coef}>
                        <div
                            className="Bets__coef-wrapper"
                            css={styles.coef_wrapper}
                        >
                            <div
                                className="Bets__coef-pred"
                                css={styles.coef_pred}
                            >
                                x
                            </div>
                            <div
                                className="Bets__coef-value"
                                css={styles.coef_value}
                            >
                                {coef2}
                            </div>
                        </div>

                        <input
                            type="text"
                            css={styles.coef_input}
                            value={coef2}
                            onChange={handleCoef2Change}
                            onBlur={() => {
                                handleBlur(coef2, setCoef2);
                            }}
                        />
                    </div>
                </div>

                <div className="Bets__item__body" css={styles.item_body}>
                    <div className="Bets__options" css={styles.options}>
                        <div
                            className="Bets__option_head"
                            css={styles.options_head}
                        >
                            <button
                                className="Bets__option_minus"
                                css={styles.options_head_btn}
                                onClick={() => {
                                    handleBet2Add(-0.1);
                                }}
                            >
                                <svg
                                    width="10"
                                    height="2"
                                    viewBox="0 0 10 2"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                >
                                    <path d="M10 2H0V0H10V2Z" fill="#948AC5" />
                                </svg>
                            </button>
                            <div
                                className="Bets__options-value-wrapper"
                                css={styles.bet_options_wrapper}
                            >
                                <input
                                    type="text"
                                    value={bet2}
                                    onChange={handleBet2Change}
                                    css={styles.bet_input}
                                    onBlur={() => {
                                        handleBlur(bet2, setBet2);
                                    }}
                                />

                                <div
                                    className="Bets__options-wrapper"
                                    css={styles.bet_wrapper}
                                >
                                    <span
                                        className="Bets__options-value-"
                                        css={styles.bet_wrapper_value}
                                    >
                                        {bet2}
                                    </span>

                                    <span
                                        className="Bets__options-post"
                                        css={styles.bet_wrapper_post}
                                    >
                                        $
                                    </span>
                                </div>
                            </div>
                            <button
                                className="Bets__option_minus"
                                css={styles.options_head_btn}
                                onClick={() => {
                                    handleBet2Add(0.1);
                                }}
                            >
                                <svg
                                    width="13"
                                    height="13"
                                    viewBox="0 0 13 13"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                >
                                    <path
                                        d="M7.24661 7.4694V12.7578H4.92488V7.4694H0V5.18285H4.92488V0H7.24661V5.18285H12.1715V7.4694H7.24661Z"
                                        fill="#948AC5"
                                    />
                                </svg>
                            </button>
                        </div>
                        <div
                            className="Bets__option_body"
                            css={styles.options_body}
                        >
                            <button
                                className="Bets__option_body-plus "
                                css={styles.options_body_btn}
                                onClick={() => {
                                    handleBet2Add(50);
                                }}
                            >
                                +50
                            </button>
                            <button
                                className="Bets__option_body-plus"
                                css={styles.options_body_btn}
                                onClick={() => {
                                    handleBet2Add(100);
                                }}
                            >
                                +100
                            </button>
                            <button
                                className="Bets__option_body-plus"
                                css={styles.options_body_btn}
                                onClick={() => {
                                    handleBet2Add(200);
                                }}
                            >
                                +200
                            </button>
                            <button
                                className="Bets__option_body-plus"
                                css={styles.options_body_btn}
                                onClick={() => {
                                    handleBet2Add(500);
                                }}
                            >
                                +500
                            </button>
                        </div>
                    </div>
                    <button className="Bets__btn" css={styles.bets_btn} onClick={props.showRegistration}>
                        <svg
                            width="39"
                            height="12"
                            viewBox="0 0 39 12"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                        >
                            <path
                                d="M34.7392 11.614H30.821V2.38235H26.5602V0H39V2.38235H34.7392V11.614Z"
                                fill="white"
                            />
                            <path
                                d="M25.0187 9.23162V11.614H14.431V0H25.0187V2.38235H18.3599V4.69227H24.6226V6.86536H18.3599V9.23162H25.0187Z"
                                fill="white"
                            />
                            <path
                                d="M7.37607 11.614H0V0H7.18337C10.2237 0 12.0544 1.10264 12.0544 2.89746C12.0544 4.20131 10.7483 5.23957 9.03541 5.41664V5.48102C11.123 5.55346 12.7716 6.72049 12.7716 8.2658C12.7716 10.3021 10.6841 11.614 7.37607 11.614ZM3.92891 2.09261V4.73251H5.95224C7.37607 4.73251 8.2325 4.22546 8.2325 3.38842C8.2325 2.57552 7.46171 2.09261 6.14494 2.09261H3.92891ZM3.92891 9.52137H6.2627C7.87922 9.52137 8.77848 8.99017 8.77848 8.00825C8.77848 7.05853 7.85781 6.52733 6.24129 6.52733H3.92891V9.52137Z"
                                fill="white"
                            />
                        </svg>
                    </button>
                </div>
            </div>
        </div>
    );
}

export default Bets;
