import React, { useEffect, useRef, useState } from 'react';
import frameBg from '../assets/images/jet-frame.svg';
import frameBg90 from '../assets/images/jet-frame-90deg.svg';
import clouds from '../assets/images/clouds.svg';
import stars from '../assets/images/stars.png';
import JetContent from './JetContent';

function Jet(props) {
    const styles = {
        jet: {
            position: 'relative',
            paddingLeft: '20px',
            paddingBottom: '20px',
            height: '100%',
            width: '100%',
           
        },
        jet_stars: {
            background: `url(${stars}) 0% 0% / contain repeat-x`,
            animation: '370370s linear 0s infinite normal none running gKBsdr',
            position: 'absolute',
            width: '100%',
            height: '100%',
            animationTimeline: 'auto',
            animationRangeStart: 'normal',
            animationRangeEnd: 'normal',
        },
        jet_frame_1: {
            position: 'absolute',
            bottom: '0px',
            left: '0px',
            width: '20px',
            height: '100%',
            animation: '3s linear 0s infinite normal none running slideFrameY',
            background: `url(${frameBg90})`,
            borderRight: '1px solid rgb(28, 21, 55)',
            backgroundRepeat: 'repeat-y',
        },
        jet_frame_2: {
            position: 'absolute',
            bottom: '0px',
            left: '20px',
            width: '100%',
            height: '20px',
            background: `url(${frameBg})`,
            animation: '3s linear 0s infinite normal none running slideFrameX',
            borderTop: '1px solid rgb(28, 21, 55)',
            backgroundRepeat: 'repeat-x',
        },
        jet_clouds: {
            position: 'absolute',
            width: '100%',
            height: '157px',
            bottom: '0px',
            opacity: '1',
            background: `url(${clouds}) 0% repeat-x`,
            animation: '37037s linear 0s infinite normal none running slideClouds',
            transition: 'background-position-y 0.64s ease 0s, opacity 0.64s ease 0s',
        },
    };

    const jetContRef = useRef(null);

    const [deliverObject, setDeliverObject] = useState({
        height: 800,
        width: 700,
    });

    useEffect(() => {
        if (jetContRef.current) {
            const newDeliverObject = {
                height: jetContRef.current.offsetHeight,
                width: jetContRef.current.offsetWidth,
            };
            setDeliverObject(newDeliverObject);
        } else {
            console.log('something went wrong');
        }
    }, []);

    return (
        <div className="Jet" style={styles.jet}>
            <div className='Jet__stars' style={styles.jet_stars}></div>
            <div className="Jet__frame Jet__frame_1" style={styles.jet_frame_1}></div>
            <JetContent
                jetCounterTo={props.jetCounterTo}
                showJet={props.showJet}
                showLoading={props.showLoading}
                addCoef={props.addCoef}
                ref={jetContRef}
                deliverObject={deliverObject}
            />
            <div className="Jet__frame Jet__frame_2" style={styles.jet_frame_2}></div>
            <div className="Jet__clouds" style={styles.jet_clouds}></div>
        </div>
    );
}

export default Jet;