/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

import { useState } from "react";

import colors from "./supporting/colors";

import Logo from "./components/Logo";
import Options from "./components/Options";
import Online from "./components/Online";
import BetsList from "./components/BetsList";
import Comments from "./components/Comments";
import Coef from "./components/Coef";
import Bets from "./components/Bets";
import JetMain from "./components/JetMain";
import Registration from "./components/Registration";
// import Modal from "./components/Modal";

function App() {
    let grid_gap = "1px";

    let bgColorsArray = [
        "07b889",
        "85153e",
        "b11bc6",
        "e38a00",
        "82c961",
        "9bbd4b",
        "85153e",
        "d04d1a",
        "683238",
        "53c403",
        "93942d",
        "d02f85",
        "020d69",
        "1b6e1c",
    ];

    function randomizeBgColor() {
        let id = Math.floor(Math.random() * bgColorsArray.length);
        return id;
    }

    const [coefArray, setCoefArray] = useState([
        1.34, 1.0, 1.39, 2.31, 21.43, 255.23, 1.99, 2.69, 2.51, 1.0, 2.96, 1.55,
        1.03, 1.3, 1.03, 1.3, 60.61, 1.34, 1.0, 1.39, 2.31, 21.43, 255.23, 1.99,
        2.69, 2.51, 1.0, 2.96, 1.55, 1.03, 1.3, 1.03, 1.3, 60.61,
    ]);

    function addCoef(newCoef) {
        setCoefArray([newCoef, ...coefArray]);
    }

    const [isCommentsShow, setIsCommentsShow] = useState(false);
    const hideComments = () => setIsCommentsShow(false);
    const showComments = () => setIsCommentsShow(true);


    const [isRegistrationShow, setIsRegistrationShow] = useState(false);
    const hideRegistration = () => setIsRegistrationShow(false);
    const showRegistration = () => setIsRegistrationShow(true);



    const styles = {
        app: css({
            width: "100%",
            background: colors.appBg,
            display: "grid",
            gridTemplateColumns: "1fr",
            gridGap: grid_gap,
            minHeight: "100vh",
        }),
        app_content: css({
            display: "grid",
            gridAutoRows: "auto",
        }),
        app_header: css({
            height: "fit-content",
            padding: "10px 10px 10px 10px",
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "space-between",
            background: colors.blocksBg,
            borderBottom: "#7862C9 solid 0.25px",
        }),
        app_online: css({
            display: "grid",
            gridAutoRows: `minmax(${colors.row_height}, auto) 1fr`,
            gridGap: grid_gap,
        }),
        app_center: css({
            background: colors.blocksBg,
            display: "grid",
            gridTemplateRows: "auto 1fr auto",
            overflow: "hidden",
            borderRight: isCommentsShow ? "rgb(28, 21, 55) 1px solid" : "unset",
        }),
    };


    

    return (
        <div className="App" css={styles.app}>
                {isCommentsShow === false && (

            <div className="App__content" css={styles.app_content}>
                <header className="App__header" css={styles.app_header}>
                    <Logo colors={colors} />
                    <Options
                        colors={colors}
                        showComments={showComments}
                        isCommentsShow={isCommentsShow}
                    />

                    
                </header>

                        <div className="App__center" css={styles.app_center}>
                            <Coef
                                coefArray={coefArray}
                                isCommentsShow={isCommentsShow}
                            />
                            <JetMain addCoef={addCoef} />
                            <Bets showRegistration={showRegistration} />
                        </div>

                        <BetsList
                            colors={colors}
                            styling={{ bgColorsArray, randomizeBgColor }}
                            
                        />
                
            </div>
        )}
        
            {isCommentsShow && (
                <div className="App__online" css={styles.app_online}>
                    <Online colors={colors} hideComments={hideComments} />
                    <Comments
                        colors={colors}
                        styling={{ bgColorsArray, randomizeBgColor }}
                        showRegistration={showRegistration}
                    />
                </div>
            )}


            {isRegistrationShow && (
                <Registration hideRegistration={hideRegistration}/>
            )}
        </div>
    );
}

export default App;
