/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { useState } from "react"

function Coef(props) {

    let styles = {
        coef:css({
            overflow: 'hidden',
            position: 'relative',
            borderBottom: '#7862C9 1px solid',
        }),
        coef_list_wrapper:css({
            padding: '16px 0px',
            overflow: 'hidden',
            marginRight: '12px',
            marginLeft: '12px',
        }),
        coef_list:css({
            display: 'flex',
            flexDirection: 'row',
            transition: '0.32s',
        }),
        // without 'css' because using at map for coefsList 
        coef_item:{
            minWidth: '53px',
            padding: '5px 6px 6px',
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
            background: '#3a5cc8',
            marginRight: '4px',
            fontSize: '11px',
            borderRadius: '7px',
        },
        coef_btn:css({
            position: 'absolute',
            top: '50%',
            right: '11px',
            transform: 'translateY(-50%)',

            background: '#231d47',
            border: '3px #0C0918 solid',
            borderRadius: '8px',

            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',

            width: 'calc(40px - 3px * 2)',
            height: 'calc(40px - 3px * 2)',
        }),
        coef_btn_svg: css({
            width: '21px',
        }),
    }




    let coefsList = props.coefArray.map((item, id)=>{
        let bgOrange = css({
            ...styles.coef_item,
            background: "#e38a00",
        })
        let bgPurple = css({
            ...styles.coef_item,
            background: "#753fc5",
        })

        let bgActive = {...styles.coef_item}
        if(item >= 2){
            bgActive = bgPurple
        }
        if(item >= 10){
            bgActive = bgOrange
        }
        


        return(
            <div css={bgActive} key={`${id}-${item}`}>
                {`${item}x`}
            </div>
        )
    })


    return (
        <div className="Coef" css={styles.coef}>
            <div className="Coef__list-wrapper" css={styles.coef_list_wrapper}>
                <div className="Coef__list" css={styles.coef_list}>{coefsList}</div>
            </div>
            <button css={styles.coef_btn}>
                <svg css={styles.coef_btn_svg} width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fillRule="evenodd" clipRule="evenodd" d="M8.00005 1.70005C4.52065 1.70005 1.70005 4.52065 1.70005 8.00005C1.70005 11.4794 4.52065 14.3 8.00005 14.3C11.4794 14.3 14.3 11.4794 14.3 8.00005C14.3 4.52065 11.4794 1.70005 8.00005 1.70005ZM0.300049 8.00005C0.300049 3.74746 3.74746 0.300049 8.00005 0.300049C12.2526 0.300049 15.7 3.74746 15.7 8.00005C15.7 12.2526 12.2526 15.7 8.00005 15.7C3.74746 15.7 0.300049 12.2526 0.300049 8.00005ZM8.00005 3.10005C8.38665 3.10005 8.70005 3.41345 8.70005 3.80005V7.56742L11.1131 8.77395C11.4589 8.94684 11.599 9.36731 11.4261 9.7131C11.2533 10.0589 10.8328 10.199 10.487 10.0261L7.687 8.62615C7.44985 8.50757 7.30005 8.26519 7.30005 8.00005V3.80005C7.30005 3.41345 7.61345 3.10005 8.00005 3.10005Z" fill="#9287C2" />
                </svg>
            </button>
        </div>
    );
}

export default Coef;
