/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { useEffect } from "react";
import loadingImg from "../assets/images/jet-loading.svg";

function JetLoading(props) {
    const styles = {
        loading: css({
            display: "grid",
            placeContent: "center",
            placeItems: "center",
            padding: "0px 20px",
            width: "100%",
            height: "100%",
            transition: "opacity 0.32s",
            zIndex: 100,
            gap: "24px",
            "@media (max-width: 960px)": {
                gap: "20px",
            },
            "@media (max-width: 700px)": {
                gap: "16px",
            },
            "@media (max-width: 500px)": {
                gap: "10px",
            },
            "@media (max-width: 400px)": {
                gap: "4px",
            },
        }),
        loadingImg: css({
            width: "150px",
            "@media (max-width: 960px)": {
                width: "120px",
                
            },
            "@media (max-width: 700px)": {
                width: "100px",
            },
            "@media (max-width: 500px)": {
                width: "80px",
            },
        }),
        loadingText: css({
            fontSize: "28px",
            fontWeight: 600,
            lineHeight: "39px",
            letterSpacing: "0.06em",
            textAlign: "center",
            color: "rgb(255, 255, 255)",
            textTransform: "uppercase",
            transform: "scaleY(0.90)",
            "@media (max-width: 960px)": {
                fontSize: "22px",
                lineHeight: "140%",
            },
            "@media (max-width: 700px)": {
                fontSize: "18px",
            },
            "@media (max-width: 500px)": {
                fontSize: "16px",
                

            },
        }),
        barWrap: css({
            width: "256px",
            background: "rgba(255, 255, 255, 0.1)",
            borderRadius: "24px",
            height: "6px",
            position: "relative",
        }),
        barCont: css({
            position: "relative",
            left: "1px",
            top: "1px",
            background: "rgb(148, 78, 245)",
            borderRadius: "10px",
            height: "4px",
            animation: "shrink 3s", 
        }),
    };

    useEffect(() => {
        const timer = setTimeout(() => {
            props.showJet(true);
            props.showLoading(false);
        }, 2700);
        
        return () => clearTimeout(timer); 
    }, [props]);

    return (
        <div className="JetLoading font1" css={styles.loading}>
            <img css={styles.loadingImg} src={loadingImg} alt="Loading" />
            <p css={styles.loadingText} className="font2">Waiting for the next round</p>
            <div css={styles.barWrap}>
                <div css={styles.barCont}></div>
            </div>
        </div>
    );
}

export default JetLoading;