/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

import animationIcon from "../../assets/images/menuAnimation.svg";
import rulesIcon from "../../assets/images/menuRules.svg";
import historyIcon from "../../assets/images/menuHistory.svg";
import limitsIcon from "../../assets/images/menuLimits.svg";
import { useState } from "react";


function Menu(props) {

    const [isAnimation, setIsAnimation] = useState(true)

    const styles = {
        menu: css({
            width: '270px',
            // position: 'fixed',
            // bottom: '0',
            // left: '50%',
            // transform: 'translate(-50%, -50%)',
            borderRadius: '13px',
            background: '#1B1536',
            fontFamily: "SF Pro Display",

            position: 'absolute',
            zIndex: '12',
            right: '49px',
            top: '40px',
        }),
        head: css({
            padding: '16px 16px 12px',
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'space-between',
            alignItems: 'center',
        }),
        headIcon: css({
            background: '#0D9A5F',
            fontSize: '24px',
            fontWeight: '500',
            padding: '9px 8px 8px',
            borderRadius: '50%',
            // width: '46px',
            // height: '46px',
            width: 'fit-content',
        }),
        headName: css({
            color: 'rgba(255, 255, 255, 0.69)',
            fontSize: '16px',
            fontWeight: '500',
        }),
        body: css({}),
        // body: css({}),
        group: css({
            marginTop: '8px',
            paddingBottom: '10px',
            paddingTop: '4px',
            borderRadius: '0px 0px 13px 13px',
            background: '#211A42',

        }),
        button: css({
            width: '100%',
            background: '#211A42',
            display: 'grid',
            gridTemplateColumns: 'auto 1fr auto',
            padding: '7px 20px 7px 16px',
            border: 'none',
            outline: 'none',
            alignItems: 'center',
            cursor: 'pointer',
        }),
        buttonText: css({
            fontFamily: "SF Pro Display",
            color: 'rgba(255, 255, 255, 0.69)',
            fontSize: '16px',
            fontWeight: '500',
            textAlign: 'start',
            marginLeft: '12px'
        }),
        toggleWrapper: css({
            padding: '1px',
            width: '30px',
            height: '19px',
            borderRadius: '5px',
            background: '#160E35',
            position: 'relative',
        }),
        toggle: css({
            width: '15px',
            height: '15px',
            borderRadius: '5px',
            background: '#944EF5',
            transition: 'left 0.32s, opacity 0.32s, background 0.32s',
            position: 'absolute',
            top: '50%',
            left: isAnimation ? '2px' : 'calc(100% - 17px)'  ,
            transform: 'translateY(-50%)',
            opacity: isAnimation ? '0.3' : '1' 
        }),

    }

    const openRules = () => {
        props.controls.modalRules.show()
        props.controls.menu.hide()
    }
    const openHistory = () => {
        props.controls.modalHistory.show()
        props.controls.menu.hide()
    }
    const openLimits = () => {
        props.controls.modalLimits.show()
        props.controls.menu.hide()
    }

    const toggleAnimation = () =>{
        if(isAnimation){
            setIsAnimation(false)
        }else{
            setIsAnimation(true)
        }
    }

    return (
        <>
            {props.isVisible && (
                <div className='Menu' css={styles.menu}>
                    <div css={styles.head}>
                        <div css={styles.headIcon}>
                            DE
                        </div>
                        <p css={styles.headName}>demo</p>
                    </div>

                    <div css={styles.body}>
                        <button css={styles.button} onClick={toggleAnimation}>
                            <img src={animationIcon} alt="animation" />
                            <p css={styles.buttonText}>Animation</p>
                            <div css={styles.toggleWrapper} >
                                <div css={styles.toggle}></div>
                            </div>
                        </button>

                        <div css={styles.group}>
                            <button css={styles.button} onClick={openRules}>
                                <img src={rulesIcon} alt="rules" />
                                <p css={styles.buttonText}>Rules of the game</p>
                            </button>
                            <button css={styles.button} onClick={openHistory}>
                                <img src={historyIcon} alt="history" />
                                <p css={styles.buttonText}>Betting history</p>
                            </button>
                            <button css={styles.button} onClick={openLimits}>
                                <img src={limitsIcon} alt="limits" />
                                <p css={styles.buttonText}>Game limits</p>
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </>
    )
}

export default Menu;