/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";

function Online(props) {
    const styles = {
        online: css({
            background: props.colors.blocksBg,
            display: 'grid',
            gridTemplateColumns: '1fr auto',
            paddingLeft: '17px',
            paddingRight: '17px',
        }),
        grid: css({
            display: 'grid',
            gridTemplateColumns: 'auto 1fr',
            alignItems: 'center',
            gridGap: '9px',
        }),
        gridIndicator: css({
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
        }),
        onlineTitle: css({
            fontWeight: 900,
            fontSize: '15px',
        }),
        onlineDescription: css({
            fontWeight: 600,
            fontSize: '11px',
            color: '#666b81',
        }),
        hideButton: css({
            alignSelf: 'center',
        }),
    };

    const hideCommentsBtnHandler = () => {
        props.hideComments();
    };

    return (
        <div className="Online" css={styles.online}>
            <div className="Online__grid" css={styles.grid}>
                <div className="Online__grid-indicator" css={styles.gridIndicator}>
                    <svg
                        width="26"
                        height="26"
                        viewBox="0 0 26 26"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                    >
                        <circle
                            cx="13"
                            cy="13"
                            r="13"
                            fill="#DE8B03"
                            fillOpacity="0.39"
                        />
                        <circle cx="13" cy="13" r="6" fill="#DE8B03" />
                    </svg>
                </div>
                <div className="Online__grid-col">
                    <p className="Online__grid-title" css={styles.onlineTitle}>
                        2333
                    </p>
                    <p className="Online__grid-description" css={styles.onlineDescription}>
                        Players online:
                    </p>
                </div>
            </div>

            <div
                className="Online__btn-hide btn btn_single"
                css={styles.hideButton}
                onClick={hideCommentsBtnHandler}
            >
                <svg
                    width="11"
                    height="15"
                    viewBox="0 0 11 15"
                    style={{ marginRight: "-1px" }}
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <path
                        d="M1 1L9 8L1 14"
                        stroke="#968FB0"
                        strokeWidth="2"
                        strokeLinecap="round"
                    />
                </svg>
            </div>
        </div>
    );
}

export default Online;