/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import colors from "./supporting/colors";

import Logo from "./components/Logo";
import Options from "./components/Options";
import Online from "./components/Online";
import BetsList from "./components/BetsList";
import Comments from "./components/Comments";
import Coef from "./components/Coef";
import Bets from "./components/Bets";

import JetMain from "./components/JetMain";
import { useState } from "react";
import { isVisible } from '@testing-library/user-event/dist/utils';
import ModalHistory from './components/modals/ModalHistory';

function App() {


    let bgColorsArray = [
        "07b889", "85153e", "b11bc6", "e38a00",
        "82c961", "9bbd4b", "85153e", "d04d1a",
        "683238", "53c403", "93942d", "d02f85",
        "020d69", "1b6e1c",
    ];

    function randomizeBgColor() {
        let id = Math.floor(Math.random() * bgColorsArray.length);
        return id;
    }

    const [coefArray, setCoefArray] = useState([
        1.34, 1.0, 1.39, 2.31, 21.43, 255.23,
        1.99, 2.69, 2.51, 1.0, 2.96, 1.55,
        1.03, 1.3, 1.03, 1.3, 60.61, 1.34,
        1.0, 1.39, 2.31, 21.43, 255.23, 1.99,
        2.69, 2.51, 1.0, 2.96, 1.55, 1.03,
        1.3, 1.03, 1.3, 60.61,
    ]);

    function addCoef(newCoef) {
        setCoefArray([newCoef, ...coefArray]);
    }

    const [isCommentsShow, setIsCommentsShow] = useState(true);
    const hideComments = () => setIsCommentsShow(false);
    const showComments = () => setIsCommentsShow(true);


    const [isMenuShow, setIsMenuShow] = useState(false);
    const menuControl = {
        isVisible: isMenuShow,
        hide: () => setIsMenuShow(false),
        show: () => setIsMenuShow(true),
    };

    const [isModalFairnessShow, setIsModalFairnessShow] = useState(true);
    const modalFairnessControl = {
        isVisible: isModalFairnessShow,
        hide: () => setIsModalFairnessShow(false),
        show: () => setIsModalFairnessShow(true),
    };

    const [isModalRulesShow, setIsModalRulesShow] = useState(false);
    const modalRulesControl = {
        isVisible: isModalRulesShow,
        hide: () => setIsModalRulesShow(false),
        show: () => setIsModalRulesShow(true),
    };
    const [isModalHistoryShow, setIsModalHistoryShow] = useState(false);
    const modalHistoryControl = {
        isVisible: isModalHistoryShow,
        hide: () => setIsModalHistoryShow(false),
        show: () => setIsModalHistoryShow(true),
    };
    const [isModalLimitsShow, setIsModalLimitsShow] = useState(false);
    const modalLimitsControl = {
        isVisible: isModalLimitsShow,
        hide: () => setIsModalLimitsShow(false),
        show: () => setIsModalLimitsShow(true),
    };

    const controls = {
        menu: menuControl,
        modalFairness: modalFairnessControl,
        modalRules: modalRulesControl,
        modalHistory: modalHistoryControl,
        modalLimits: modalLimitsControl,
    };

    const styles = {
        app: css({
            width: '100%',
            height: '100%',
            background: colors.appBg,
            display: 'grid',
            gridTemplateColumns: '1fr auto',
            gridGap: '1px',
        }),
        app_content: css({
            display: 'grid',
            gridTemplateColumns: '300px 1fr',
            gridAutoRows: `minmax(${colors.row_height}, auto) 1fr`,
            gridGap: '1px',
        }),
        app_online: css({
            display: 'grid',
            gridTemplateColumns: '232px',
            gridAutoRows: `minmax(${colors.row_height}, auto) 1fr`,
            gridGap: '1px',
        }),
        app_center: css({
            background: colors.blocksBg,
            maxWidth: isCommentsShow ? 'calc(100vw - 300px - 232px)' : '100%',
            display: 'grid',
            gridTemplateRows: 'auto 1fr auto',
            overflow: 'hidden',
            borderRight: isCommentsShow ? 'rgb(28, 21, 55) 1px solid' : 'unset',
        }),
    };

    // const is


    return (
        <div className="App" css={styles.app}>
            <div className="App__content" css={styles.app_content}>
                <Logo colors={colors} />
                <Options
                    colors={colors}
                    showComments={showComments}
                    isCommentsShow={isCommentsShow}

                    controls={controls}
                />
                <BetsList
                    colors={colors}
                    styling={{ bgColorsArray, randomizeBgColor }}
                />
                <div className="App__center" css={styles.app_center}>
                    <Coef
                        coefArray={coefArray}
                        isCommentsShow={isCommentsShow}
                    />
                    <JetMain addCoef={addCoef} />
                    <Bets />
                </div>
            </div>
            {isCommentsShow && (
                <div className="App__online" css={styles.app_online}>
                    <Online colors={colors} hideComments={hideComments} />
                    <Comments
                        colors={colors}
                        styling={{ bgColorsArray, randomizeBgColor }}
                    />
                </div>
            )}


        </div>
    );
}

export default App;